.head {
  display: flex;
  flex-direction: row;
  margin-bottom: 15px;
  padding-top: 20px;
  nav {
    flex: 0 calc(100% - 200px);
    display: flex;
    justify-content: center;
    margin-left: 100px;
    a {
      color: whitesmoke;
      text-decoration: none;
      padding: 0 10px;
      font-size: 1.2rem;
    }
    a.active {
      text-decoration: underline;
    }
  }
}
