// Reset START
button {
  border: unset;
  padding: unset;
  background: unset;
  cursor: pointer;
  color: #f6f4f1;
}

h1,h2,h3,h4,h5,h6 {
  margin: unset;
  padding: unset;
}
// Reset END

// Global START
:global(#root) {
  width: 100%;
  max-width: 1440px;
  margin: auto;
}

body {
  margin: 0;
  height: 100vh;
  background: #2b2d2e;
  color: #f6f4f1;
}

a {
  color: whitesmoke;
}

div {
  background: #2b2d2e;
}

input[type="text"] {
  background: transparent;
  color: whitesmoke;
}
// Global END

:global(.content) {
  display: flex;
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

:global(.navigation) {
  display: flex;
  flex-direction: row;
}
