.modalBackground {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background: #000000b8;
  z-index: 999;
  .modal {
    position: relative;
    width: 500px;
    min-width: 500px;
    padding: 31px 25px 25px 25px;
    border-radius: 6px;
    box-sizing: border-box;
    z-index: 999;
    .close {
      position: absolute;
      top: 20px;
      right: 20px;
    }
    .title {
      margin-bottom: 30px;
    }
    .subtitle {
      display: flex;
      justify-content: space-between;
      .unit {
        margin-bottom: 20px;
      }
    }
    .order {
      display: flex;
      flex-direction: column;
      .orderUnit {
        display: flex;
        justify-content: space-between;
      }
    }
    .total {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      margin-top: 40px;
    }
  }
}
